/* eslint-disable react/no-danger */
import React, { useState, useEffect } from 'react';
import { Tag } from 'antd';
import Card from '../../../components/common/Card/index';
import CardTitle from '../../../components/common/CardTitle/index';
import { getQueryParams } from '../../../react-uikit/utils/urlUtil';
import { getPortalNewsById } from '../../../services/portalNews';
import './styles.scss';
import { getFileUrl } from '../../../services/filestorage';
import { dateTimeToString } from '../../../utils/formatUtil';

export default () => {
  const [news, setNews] = useState({});
  const { id } = getQueryParams();
  const callGetPortalNewsById = async () => {
    if (!id) {
      return;
    }
    const [res, err] = await getPortalNewsById({ id });
    if (err) {
      return;
    }
    setNews(res.data);
  };
  useEffect(() => {
    callGetPortalNewsById();
  }, [id]);
  return (
    <div className="page-NewsDetail">
      <Card>
        <CardTitle title={news.title} />
        <div style={{ float: 'left' }}>Ngày đăng: {dateTimeToString(news.startDate)}</div>
        <div style={{ padding: '0 15px 15px 15px' }}>
          <div style={{ marginBottom: 15, textAlign: 'right' }}>
            {(news.categories || []).map(cate => {
              return (
                <Tag
                  style={{
                    padding: '5px 15px',
                    borderRadius: '25px',
                    fontWeight: 'bold',
                    border: '0',
                    fontSize: '13px',
                    color: '#7687b5',
                    background: '#d3d9e954',
                  }}
                >
                  {cate.label}
                </Tag>
              );
            })}
          </div>
          <div className="news-content" dangerouslySetInnerHTML={{ __html: news.description }} />

          {news.attachments && news.attachments.length > 0 && (
            <div style={{ marginTop: 15 }}>
              <strong style={{ fontSize: 14 }}>Tệp đính kèm</strong>
              <br />
              {news.attachments.map(file => {
                return (
                  <a
                    className="btn-download-file"
                    target="_blank"
                    href={getFileUrl(file.fileId)}
                    rel="noreferrer"
                  >
                    {file.fileName}
                  </a>
                );
              })}
            </div>
          )}
        </div>
      </Card>
    </div>
  );
};



