import { request } from '../utils/requestToJs';

const prefix = '/api/education';

export const getPortalNews = ({
  title,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/PortalNews`,
    method: 'GET',
    params: { title, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createPortalNews = data =>
  request({
    prefix,
    url: `/PortalNews`,
    method: 'POST',
    params: {},
    data,
  });

export const getPortalNewsById = ({ id } = {}) =>
  request({
    prefix,
    url: `/PortalNews/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const updatePortalNews = (id, data) =>
  request({
    prefix,
    url: `/PortalNews/${id}`,
    method: 'PUT',
    params: {},
    data,
  });

export const deletePortalNews = ({ id } = {}) =>
  request({
    prefix,
    url: `/PortalNews/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



