import { hostAPI, request } from '../utils/requestToJs';
import { requestDownloadFile } from '../utils/request';

export const getAvatarUrl = id => {
  if (!id) {
    return '/resources/images/avatar-default.png';
  }
  return `${hostAPI}/api/filestorage/Storage/${id}/Download`;
};

export const getFileUrl = id => {
  if (!id) {
    return '';
  }
  return `${hostAPI}/api/filestorage/Storage/${id}/Download`;
};

export const uploadFile = data =>
  request({
    url: `/api/filestorage/Storage/Upload`,
    method: 'POST',
    params: {},
    data,
  });

export const downloadFile = id =>
  requestDownloadFile({
    url: `/api/filestorage/Storage/${id}/Download`,
    method: 'GET',
    params: {},
    data: {},
  });


